* {
  padding: 0px 10px;
}

.main-screen {
  display: flex;
  flex-direction: column;
}

.guess-color-text {
  width: 100%;
  height: fit-content;
  background-color: #80808099;
  text-align: center;
  padding: 10px;
}

.hr {
  width: 100%;
  border-width: 2px;
}

.guess-color {
  background-color: red;
  width: 100%;
  height: 100px;
}

.grey-color {
  width: 100%;
  height: 100%;
  background-color: #b8b8b8cc;
  z-index: 2;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.start-game {
  width: fit-content;
  height: fit-content;
  padding: 10px;
  background-color: white;
  position: relative;
  padding: 10px 22px;
  z-index: 3;
  display: flex;
  flex-direction: column;
}

.start-button {
  background-color: greenyellow;
  border: 2px solid white;
  border: none;
  font-size: 15px;
  padding: 10px 15px;
}

.start-button:hover {
  background-color: white;
  border: 2px solid greenyellow;
}

.guess-color {
  background-color: rgb(200, 220, 139);
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: center;
  margin-top: 16px
}

.question-number {
  height: fit-content;
  text-align: center;
}