.score-board {
    padding: 0px;
    display: flex;
    justify-content: flex-end;
}

.score-board-positive {
    padding: 0px;
}

.score-board-positive,
.score-board-negative {
    margin: 0px;
    padding: 0px;
}

.score-board-positive {
    color: darkgreen;
}

.score-board-negative {
    color: red;
}