.choose-color {
  height: fit-content;
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.single-color {
  width: 300px;
  margin: 10px;
  height: 300px;
  background-color: #7e2f2f;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
}
.single-color:hover {
  box-shadow: 9px 10px #8888888c;
}
.choose-color-name {
  width: fit-content;
  height: fit-content;
}
