.viewStateSwap {
    display: flex;
    font-weight: bold;
    cursor: pointer;
    left: 20px;
    top: 75px;
}

.view-state {
    margin-left: 10px;
    width: 90px;
    padding: 0px 30px 6px 5px;
    border-bottom: 3px solid transparent;
}

.view-state.selected-state {
    padding-bottom: 0px;
}

.view-state.selected-state[name="view-active"] {
    color: darkgreen;
    border-bottom-color: darkgreen;
}

.view-state.selected-state[name="view-success"] {
    color: blue;
    border-bottom-color: blue;
}

.view-state:not(.selected-state) {
    color: #888888;
}

.view-state:not(.selected-state):hover {
    border-bottom: 3px solid #888888;
}